<template>
  <div class="flex flex-row items-center br1 darken-10 ph3 hide-child">
    <div class="flex-fill mr3">
      <md-input-container class="css-condensed">
        <md-select
          style="min-width: auto"
          v-model="hour"
        >
          <md-option
            v-for="(h, index) in hour_choices"
            :value="h.val"
          >
            {{h.label}}
          </md-option>
        </md-select>
      </md-input-container>
    </div>
    <div class="flex-fill mr3">
      <md-input-container class="css-condensed">
        <md-select
          style="min-width: auto"
          v-model="minute"
        >
          <md-option
            v-for="(m, index) in minute_choices"
            :value="m"
          >
            {{m}}
          </md-option>
        </md-select>
      </md-input-container>
    </div>
    <div class="flex-fill mr3">
      <md-input-container class="css-condensed">
        <md-select
          style="min-width: auto"
          v-model="period"
        >
          <md-option
            v-for="(p, index) in period_choices"
            :value="p"
          >
            {{p}}
          </md-option>
        </md-select>
      </md-input-container>
    </div>
    <div class="flex-none ml2">
      <div
        class="pointer f3 lh-solid ph1 b hint--top"
        aria-label="Delete"
        @click="onDeleteClick"
      >
        &times;
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['item', 'index'],
    data() {
      return {
        hour: 8,
        minute: 0,
        period: 'AM',
        hour_choices: [
          { label: '1',  val: 1  },
          { label: '2',  val: 2  },
          { label: '3',  val: 3  },
          { label: '4',  val: 4  },
          { label: '5',  val: 5  },
          { label: '6',  val: 6  },
          { label: '7',  val: 7  },
          { label: '8',  val: 8  },
          { label: '9',  val: 9  },
          { label: '10', val: 10 },
          { label: '11', val: 11 },
          { label: '12', val: 0  }
        ],
        minute_choices: [0,5,10,15,20,25,30,35,40,45,50,55],
        period_choices: ['AM', 'PM']
      }
    },
    watch: {
      item(val, old_val) {
        this.$nextTick(() => { this.initFromItem() })
      },
      hour() {
        this.fireChangeEvent()
      },
      minute() {
        this.fireChangeEvent()
      },
      period() {
        this.fireChangeEvent()
      }
    },
    mounted() {
      this.initFromItem()
    },
    methods: {
      initFromItem() {
        var h = _.get(this.item, 'hour', 8)
        var m = _.get(this.item, 'minute', 0)

        this.hour = h % 12
        this.minute = m - (m % 5)
        this.period = h > 11 ? 'PM' : 'AM'
      },
      getTime() {
        var t = _.assign({}, this.$data)
        if (t.period == 'PM')
          t.hour += 12

        return _.pick(t, ['hour', 'minute'])
      },
      fireChangeEvent() {
        this.$emit('change', this.getTime(), this.index)
      },
      onDeleteClick() {
        this.$emit('delete', this.getTime(), this.index)
      }
    }
  }
</script>

<style lang="less" scoped>
  .md-input-container.css-condensed {
    padding-top: 0;
    margin-bottom: 0.75rem;
    min-height: 32px;
  }
</style>



